<template>
    <div class="form-design">
      <!-- 顶部操作栏 -->
      <div class="form-design-header">
        <a-space>
          <a-button type="primary" @click="handleSave">
            <template #icon><save-outlined /></template>
            保存
          </a-button>
          <a-button @click="handlePreview">
            <!-- <template #icon><eye-outlined /></template> -->
            预览
          </a-button>
          <a-button @click="handleClear">
            <!-- <template #icon><delete-outlined /></template> -->
            清空
          </a-button>
          <a-button 
            @click="handleUndo" 
            :disabled="!canUndo"
          >
            <!-- <template #icon><undo-outlined /></template> -->
            撤销
          </a-button>
          <a-button 
            @click="handleRedo" 
            :disabled="!canRedo"
          >
            <!-- <template #icon><redo-outlined /></template> -->
            重做
          </a-button>
          <a-dropdown>
            <a-button>
              更多操作
              <down-outlined />
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item key="1" @click="handleExport">
                  <download-outlined />导出配置
                </a-menu-item>
                <a-menu-item key="2" @click="handleImportClick">
                  <upload-outlined />导入配置
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
          <!-- 隐藏的文件输入框 -->
          <input
            type="file"
            ref="fileInput"
            style="display: none"
            accept=".json"
            @change="handleImportFile"
          >
        </a-space>
      </div>
      
      <!-- 主要内容区 -->
      <div class="form-design-content">
        <!-- 左侧组件面板 -->
        <div class="component-panel">
          <h3>基础字段</h3>
          <div class="component-list">
            <div
              v-for="item in basicComponents"
              :key="item.type"
              class="component-item"
              draggable
              @dragstart="handleComponentDragStart(item)"
              @click="handleAddComponent(item)"
            >
              <component :is="item.icon" />
              <span>{{ item.label }}</span>
            </div>
          </div>
        </div>
  
        <!-- 中间设计画布 -->
        <div 
          class="design-canvas"
          @dragover.prevent
          @drop="handleComponentDrop"
        >
          <draggable
            v-model="formItems"
            v-bind="dragOptions"
            class="form-items-container"
            @start="handleDragStart"
            @end="handleDragEnd"
            @change="handleDragChange"
          >
            <transition-group type="transition" name="flip-list">
              <div
                v-for="(item, index) in formItems"
                :key="item.id"
                class="form-item"
                :class="{ 'is-selected': selectedIndex === index }"
                @click.stop="handleSelectItem(index)"
              >
                <!-- 组件操作按钮 -->
                <div class="form-item-actions" v-show="selectedIndex === index">
                  <a-space>
                    <a-tooltip title="复制">
                      <a-button 
                        type="link" 
                        size="small"
                        @click.stop="handleCopyItem(index)"
                      >
                      复制
                        <template #icon><copy-outlined /></template>
                      </a-button>
                    </a-tooltip>
                    <a-tooltip title="删除">
                      <a-button 
                        type="link" 
                        size="small" 
                        danger
                        @click.stop="handleDeleteItem(index)"
                      >
                      删除
                        <template #icon><delete-outlined /></template>
                      </a-button>
                    </a-tooltip>
                  </a-space>
                </div>
                <div class="form-item-content">
                  <component
                    :is="item.type"
                    v-bind="item.props"
                  />
                </div>
              </div>
            </transition-group>
          </draggable>
          
          <div v-if="!formItems.length" class="empty-tip">
            从左侧拖拽或点击添加组件
          </div>
        </div>
  
        <!-- 右侧属性面板 -->
        <div class="property-panel" v-if="currentItem">
          <h3>属性设置</h3>
          <a-form layout="vertical">
            <a-form-item label="标题">
              <a-input v-model="currentItem.props.label" />
            </a-form-item>
            <a-form-item label="字段名">
              <a-input v-model="currentItem.props.name" />
            </a-form-item>
            <a-form-item label="是否必填">
              <a-switch v-model="currentItem.props.required" />
            </a-form-item>
            
            <!-- 基础属性配置 -->
            <a-collapse>
              <a-collapse-panel key="1" header="基础属性">
                <a-form-item label="占位提示">
                  <a-input v-model="currentItem.props.placeholder" />
                </a-form-item>
                <a-form-item label="禁用">
                  <a-switch v-model="currentItem.props.disabled" />
                </a-form-item>
                <template v-if="currentComponent.category === 'string'">
                  <a-form-item label="最大长度">
                    <a-input-number v-model="currentItem.props.maxLength" />
                  </a-form-item>
                  <a-form-item label="显示字数">
                    <a-switch v-model="currentItem.props.showCount" />
                  </a-form-item>
                </template>
                <template v-if="currentComponent.category === 'number'">
                  <a-form-item label="最小值">
                    <a-input-number v-model="currentItem.props.min" />
                  </a-form-item>
                  <a-form-item label="最大值">
                    <a-input-number v-model="currentItem.props.max" />
                  </a-form-item>
                  <a-form-item label="步长">
                    <a-input-number v-model="currentItem.props.step" />
                  </a-form-item>
                  <a-form-item label="精度">
                    <a-input-number v-model="currentItem.props.precision" />
                  </a-form-item>
                </template>
              </a-collapse-panel>
              
              <!-- 验证规则配置 -->
              <a-collapse-panel key="2" header="验证规则">
                <div class="rules-list">
                  <div 
                    v-for="(rule, index) in currentItem.props.rules" 
                    :key="index"
                    class="rule-item"
                  >
                    <a-select
                      v-model="rule.type"
                      style="width: 120px"
                      @change="handleRuleTypeChange(rule)"
                    >
                      <a-select-option 
                        v-for="type in availableRules" 
                        :key="type.value"
                        :value="type.value"
                      >
                        {{ type.label }}
                      </a-select-option>
                    </a-select>
                    
                    <template v-if="rule.type === 'pattern'">
                      <a-input
                        v-model="rule.pattern"
                        placeholder="请输入正则表达式"
                        style="width: 200px"
                      />
                    </template>
                    <template v-if="['min', 'max'].includes(rule.type)">
                      <a-input-number
                        v-model="rule.value"
                        placeholder="请输入数值"
                      />
                    </template>
                    
                    <a-button 
                      type="link" 
                      danger
                      @click="removeRule(index)"
                    >
                      <delete-outlined />
                    </a-button>
                  </div>
                  
                  <a-button 
                    type="dashed" 
                    block 
                    @click="addRule"
                  >
                    <plus-outlined />添加规则
                  </a-button>
                </div>
              </a-collapse-panel>
              
              <!-- 联动配置 -->
              <a-collapse-panel key="3" header="联动配置">
                <div class="linkage-config">
                  <a-form-item label="触发方式">
                    <a-select v-model="currentItem.props.linkage.trigger">
                      <a-select-option value="change">值改变时</a-select-option>
                      <a-select-option value="focus">获得焦点时</a-select-option>
                      <a-select-option value="blur">失去焦点时</a-select-option>
                    </a-select>
                  </a-form-item>
                  
                  <a-form-item label="联动动作">
                    <a-button type="dashed" block @click="addLinkageAction">
                      <plus-outlined />添加动作
                    </a-button>
                    
                    <div 
                      v-for="(action, index) in currentItem.props.linkage.actions" 
                      :key="index"
                      class="linkage-action"
                    >
                      <a-row :gutter="8">
                        <a-col :span="8">
                          <a-select v-model:value="action.targetField" style="width: 100%">
                            <a-select-option 
                              v-for="item in availableTargetFields" 
                              :key="item.name"
                              :value="item.name"
                            >
                              {{ item.label }}
                            </a-select-option>
                          </a-select>
                        </a-col>
                        <a-col :span="8">
                          <a-select v-model:value="action.type" style="width: 100%">
                            <a-select-option value="show">显示</a-select-option>
                            <a-select-option value="hide">隐藏</a-select-option>
                            <a-select-option value="required">必填</a-select-option>
                            <a-select-option value="disabled">禁用</a-select-option>
                            <a-select-option value="setValue">设置值</a-select-option>
                          </a-select>
                        </a-col>
                        <a-col :span="6">
                          <a-input 
                            v-if="action.type === 'setValue'"
                            v-model="action.value"
                            placeholder="设置的值"
                          />
                        </a-col>
                        <a-col :span="2">
                          <a-button 
                            type="link" 
                            danger
                            @click="removeLinkageAction(index)"
                          >
                            删除动作
                          </a-button>
                        </a-col>
                      </a-row>
                      
                      <a-row v-if="action.type !== 'setValue'" style="margin-top: 8px">
                        <a-col :span="24">
                          <a-form-item label="条件">
                            <a-input
                              v-model="action.condition"
                              placeholder="请输入条件表达式，例如：value === 'test'"
                            />
                          </a-form-item>
                        </a-col>
                      </a-row>
                    </div>
                  </a-form-item>
                </div>
              </a-collapse-panel>
            </a-collapse>
          </a-form>
        </div>
      </div>
  
      <!-- 预览组件 -->
      <form-preview
        :visible.sync="previewVisible"
        :form-items="formItems"
      />
    </div>
  </template>
  
  <script>
  import { 
    SaveOutlined, 
    EyeOutlined, 
    DeleteOutlined,
    CopyOutlined,
    UndoOutlined,
    RedoOutlined,
    PlusOutlined,
    DownOutlined,
    UploadOutlined,
    DownloadOutlined
  } from '@ant-design/icons-vue';
  import { basicComponents, validatorTypes } from './config/components';
  import draggable from 'vuedraggable/dist/vuedraggable.umd.min.js';
  import FormPreview from './components/FormPreview.vue';
  
  export default {
    name: 'FormDesign',
    components: {
      SaveOutlined,
      EyeOutlined,
      DeleteOutlined,
      CopyOutlined,
      draggable,
      UndoOutlined,
      RedoOutlined,
      PlusOutlined,
      DownOutlined,
      UploadOutlined,
      DownloadOutlined,
      FormPreview
    },
    data() {
      return {
        basicComponents,
        formItems: [],
        currentItem: null,
        selectedIndex: -1,
        isDragging: false,
        idCounter: 0,
        dragComponent: null,
        history: [], // 历史记录
        historyIndex: -1, // 当前历史记录索引
        maxHistory: 20, // 最大历史记录数
        previewVisible: false
      }
    },
    computed: {
      dragOptions() {
        return {
          animation: 200,
          group: 'form-items',
          disabled: false,
          ghostClass: 'ghost',
          handle: '.form-item-content'
        }
      },
      canUndo() {
        return this.historyIndex > 0;
      },
      canRedo() {
        return this.historyIndex < this.history.length - 1;
      },
      currentComponent() {
        if (!this.currentItem) return null;
        return this.basicComponents.find(c => c.type === this.currentItem.type);
      },
      availableRules() {
        if (!this.currentComponent) return [];
        return validatorTypes[this.currentComponent.category] || [];
      },
      availableTargetFields() {
        return this.formItems
          .filter(item => item.id !== this.currentItem?.id)
          .map(item => ({
            name: item.props.name,
            label: item.props.label
          }));
      }
    },
    watch: {
      // formItems 的 watch 可以删除了
    },
    methods: {
      handleComponentDragStart(component) {
        this.dragComponent = component;
        event.dataTransfer.setData('componentType', component.type);
      },
      handleComponentDrop(event) {
        event.preventDefault();
        if (this.dragComponent) {
          this.handleAddComponent(this.dragComponent);
          this.dragComponent = null;
        }
      },
      handleAddComponent(component) {
        const newItem = {
          id: `form-item-${this.idCounter++}`,
          type: component.type,
          props: { ...component.defaultProps }
        };
        const newFormItems = [...this.formItems, newItem];
        this.saveHistory(newFormItems);
        this.formItems = newFormItems;
        this.handleSelectItem(this.formItems.length - 1);
      },
      handleDragStart() {
        this.isDragging = true;
        document.body.classList.add('dragging');
      },
      handleDragEnd() {
        this.isDragging = false;
        document.body.classList.remove('dragging');
      },
      handleSelectItem(index) {
        if (this.isDragging) return;
        this.selectedIndex = index;
        this.currentItem = this.formItems[index];
      },
      handleSave() {
        // 保存表单配置
        console.log('保存的表单数据：', this.formItems);
      },
      handlePreview() {
        if (this.formItems.length === 0) {
          this.$message.warning('请先添加表单组件');
          return;
        }
        
        // 检查字段名是否都已填写
        const missingName = this.formItems.some(item => !item.props.name);
        if (missingName) {
          this.$message.warning('请为所有组件设置字段名');
          return;
        }
        
        // 检查字段名是否重复
        const names = this.formItems.map(item => item.props.name);
        const hasDuplicate = names.some((name, index) => names.indexOf(name) !== index);
        if (hasDuplicate) {
          this.$message.warning('存在重复的字段名，请修改');
          return;
        }
        
        this.previewVisible = true;
      },
      handleClear() {
        this.saveHistory([]);
        this.formItems = [];
        this.currentItem = null;
        this.selectedIndex = -1;
      },
      handleCopyItem(index) {
        const targetItem = this.formItems[index];
        const newItem = {
          id: `form-item-${this.idCounter++}`,
          type: targetItem.type,
          props: JSON.parse(JSON.stringify(targetItem.props))
        };
        
        newItem.props.label = `${newItem.props.label} Copy`;
        newItem.props.name = `${newItem.props.name}_copy`;
        
        const newFormItems = [...this.formItems];
        newFormItems.splice(index + 1, 0, newItem);
        this.saveHistory(newFormItems);
        this.formItems = newFormItems;
        this.handleSelectItem(index + 1);
      },
      handleDeleteItem(index) {
        this.$confirm({
          title: '确认删除',
          content: '是否确认删除该组件？',
          okText: '确认',
          cancelText: '取消',
          onOk: () => {
            const newFormItems = [...this.formItems];
            newFormItems.splice(index, 1);
            this.saveHistory(newFormItems);
            this.formItems = newFormItems;
            if (this.selectedIndex === index) {
              this.currentItem = null;
              this.selectedIndex = -1;
            }
          }
        });
      },
      // 历史记录相关方法
      saveHistory(items) {
        if (this.isDragging) return;
        
        const currentState = JSON.stringify(items);
        const lastState = this.history[this.historyIndex];
        
        if (currentState === lastState) return;
        
        this.historyIndex++;
        this.history.splice(this.historyIndex);
        this.history.push(currentState);
        
        if (this.history.length > this.maxHistory) {
          this.history.shift();
          this.historyIndex--;
        }
      },
      
      handleUndo() {
        if (!this.canUndo) return;
        this.historyIndex--;
        this.formItems = JSON.parse(this.history[this.historyIndex]);
      },
      
      handleRedo() {
        if (!this.canRedo) return;
        this.historyIndex++;
        this.formItems = JSON.parse(this.history[this.historyIndex]);
      },
      
      // 验证规则相关方法
      addRule() {
        if (!this.currentItem) return;
        this.currentItem.props.rules.push({
          type: 'required',
          message: '该字段为必填项'
        });
      },
      
      removeRule(index) {
        this.currentItem.props.rules.splice(index, 1);
      },
      
      handleRuleTypeChange(rule) {
        switch (rule.type) {
          case 'required':
            rule.message = '该字段为必填项';
            break;
          case 'email':
            rule.message = '请输入有效的邮箱地址';
            break;
          case 'url':
            rule.message = '请输入有效的URL地址';
            break;
          case 'pattern':
            rule.message = '请输入正确的格式';
            rule.pattern = '';
            break;
          case 'min':
            rule.message = '长度不能小于 {value}';
            rule.value = null;
            break;
          case 'max':
            rule.message = '长度不能大于 {value}';
            rule.value = null;
            break;
        }
      },
      handleDragChange(evt) {
        if (evt.moved) {
          this.saveHistory(this.formItems);
        }
      },
      handleExport() {
        const config = {
          version: '1.0.0',
          items: this.formItems
        };
        
        const blob = new Blob([JSON.stringify(config, null, 2)], {
          type: 'application/json'
        });
        
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `form-config-${new Date().getTime()}.json`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
      },
      handleImportClick() {
        this.$refs.fileInput.click();
      },
      handleImportFile(event) {
        const file = event.target.files[0];
        if (!file) return;
        
        const reader = new FileReader();
        reader.onload = (e) => {
          try {
            const config = JSON.parse(e.target.result);
            
            // 验证配置格式
            if (!config.version || !Array.isArray(config.items)) {
              throw new Error('无效的配置文件格式');
            }
            
            // 重置文件输入框
            event.target.value = '';
            
            // 导入配置
            this.formItems = config.items.map(item => ({
              ...item,
              id: `form-item-${this.idCounter++}`
            }));
            
            this.$message.success('配置导入成功');
          } catch (error) {
            this.$message.error('配置文件格式错误');
          }
        };
        reader.readAsText(file);
      },
      addLinkageAction() {
        if (!this.currentItem.props.linkage) {
          this.$set(this.currentItem.props, 'linkage', {
            trigger: 'change',
            actions: []
          });
        }
        
        this.currentItem.props.linkage.actions.push({
          targetField: '',
          type: 'show',
          condition: '',
          value: ''
        });
      },
      removeLinkageAction(index) {
        this.currentItem.props.linkage.actions.splice(index, 1);
      }
    }
  }
  </script>
  
  <style lang="less" scoped>
