    height: 100%;
    display: flex;
    flex-direction: column;
  
    &-header {
      padding: 8px 16px;
      border-bottom: 1px solid #f0f0f0;
    }
  
    &-content {
      flex: 1;
      display: flex;
      padding: 16px;
