    padding: 16px;
    overflow-y: auto;
  
    .empty-tip {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #999;
    }
  
    .form-item {
      position: relative;
      margin-bottom: 8px;
      border: 1px dashed transparent;
      border-radius: 4px;
      background: #fff;
      transition: all 0.3s;
      
      &-content {
        padding: 8px;
        cursor: move;
      }
      
      &:hover {
        border-color: #1890ff;
      }
      
      &.is-selected {
        border: 1px solid #1890ff;
        background: #f0f7ff;
      }
  
      &-actions {
        position: absolute;
        right: 8px;
