<template>
  <a-modal
    title="表单预览"
    :visible="visible"
    :width="720"
    @cancel="handleCancel"
    :footer="null"
  >
    <a-form
      ref="previewForm"
      :model="formData"
      :label-col="{ span: 6 }"
      :wrapper-col="{ span: 16 }"
    >
      <template v-for="item in formItems">
        <a-form-item
          v-show="!item.props.hidden"
          :key="item.id"
          :label="item.props.label"
          :name="item.props.name"
          :rules="transformRules(item.props.rules)"
        >
          <!-- 文本输入 -->
          <a-input
            v-if="item.type === 'a-input'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            @change="(e) => handleFieldChange(item.props.name, e.target.value)"
            @focus="(e) => handleFieldEvent(item, 'focus', e.target.value)"
            @blur="(e) => handleFieldEvent(item, 'blur', e.target.value)"
          />
          
          <!-- 多行文本 -->
          <a-textarea
            v-else-if="item.type === 'a-textarea'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            @change="(e) => handleFieldChange(item.props.name, e.target.value)"
            @focus="(e) => handleFieldEvent(item, 'focus', e.target.value)"
            @blur="(e) => handleFieldEvent(item, 'blur', e.target.value)"
          />
          
          <!-- 数字输入 -->
          <a-input-number
            v-else-if="item.type === 'a-input-number'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            style="width: 100%"
            @change="(value) => handleFieldChange(item.props.name, value)"
            @focus="() => handleFieldEvent(item, 'focus', formData[item.props.name])"
            @blur="() => handleFieldEvent(item, 'blur', formData[item.props.name])"
          />
          
          <!-- 下拉选择 -->
          <a-select
            v-else-if="item.type === 'a-select'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            style="width: 100%"
            @change="(value) => handleFieldChange(item.props.name, value)"
            @focus="() => handleFieldEvent(item, 'focus', formData[item.props.name])"
            @blur="() => handleFieldEvent(item, 'blur', formData[item.props.name])"
          >
            <a-select-option 
              v-for="opt in item.props.options"
              :key="opt.value"
              :value="opt.value"
            >
              {{ opt.label }}
            </a-select-option>
          </a-select>
          
          <!-- 日期选择 -->
          <a-date-picker
            v-else-if="item.type === 'a-date-picker'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            style="width: 100%"
            @change="(date) => handleFieldChange(item.props.name, date)"
            @focus="() => handleFieldEvent(item, 'focus', formData[item.props.name])"
            @blur="() => handleFieldEvent(item, 'blur', formData[item.props.name])"
          />
          
          <!-- 开关 -->
          <a-switch
            v-else-if="item.type === 'a-switch'"
            v-model:checked="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            @change="(checked) => handleFieldChange(item.props.name, checked)"
          />
          
          <!-- 单选框组 -->
          <a-radio-group
            v-else-if="item.type === 'a-radio-group'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            @change="(e) => handleFieldChange(item.props.name, e.target.value)"
          >
            <a-radio 
              v-for="opt in item.props.options"
              :key="opt.value"
              :value="opt.value"
            >
              {{ opt.label }}
            </a-radio>
          </a-radio-group>
          
          <!-- 复选框组 -->
          <a-checkbox-group
            v-else-if="item.type === 'a-checkbox-group'"
            v-model:value="formData[item.props.name]"
            v-bind="getComponentProps(item)"
            @change="(values) => handleFieldChange(item.props.name, values)"
          >
            <a-checkbox
              v-for="opt in item.props.options"
              :key="opt.value"
              :value="opt.value"
            >
              {{ opt.label }}
            </a-checkbox>
          </a-checkbox-group>
        </a-form-item>
      </template>

      <div class="form-actions">
        <a-space>
          <a-button @click="handleReset">重置</a-button>
          <a-button type="primary" @click="handleSubmit">提交</a-button>
        </a-space>
      </div>
    </a-form>
  </a-modal>
</template>

<script>
export default {
  name: 'FormPreview',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    formItems: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      formData: {}
    }
  },
  watch: {
    visible(val) {
      if (val) {
        this.initFormData();
      }
    }
  },
  methods: {
    initFormData() {
      const data = {};
      this.formItems.forEach(item => {
        if (item.props.name) {
          // 根据组件类型设置初始值
          switch (item.type) {
            case 'a-switch':
              data[item.props.name] = false;
              break;
            case 'a-checkbox-group':
              data[item.props.name] = [];
              break;
            case 'a-input-number':
              data[item.props.name] = null;
              break;
            default:
              data[item.props.name] = '';
          }
        }
      });
      this.formData = data;
    },
    
    transformRules(rules) {
      if (!rules) return [];
      return rules.map(rule => {
        const newRule = { ...rule };
        if (rule.type === 'required') {
          newRule.required = true;
          delete newRule.type;
        }
        return newRule;
      });
    },
    
    getComponentProps(item) {
      const props = { ...item.props };
      // 移除表单项特有的属性
      delete props.label;
      delete props.name;
      delete props.rules;
      return props;
    },
    
    handleSubmit() {
      this.$refs.previewForm
        .validate()
        .then(() => {
          this.$message.success('表单验证通过');
          console.log('表单数据：', this.formData);
        })
        .catch(error => {
          console.log('表单验证失败：', error);
        });
    },
    
    handleReset() {
      this.$refs.previewForm.resetFields();
    },
    
    handleCancel() {
      this.$emit('update:visible', false);
    },
    
    // 处理字段事件（focus/blur）
    handleFieldEvent(item, eventType, value) {
      if (item.props.linkage && item.props.linkage.trigger === eventType) {
        this.handleLinkage(item, value);
      }
    },
    
    // 处理组件值变化
    handleFieldChange(field, value) {
      // 更新表单数据
      this.formData[field] = value;
      
      // 处理联动
      this.formItems.forEach(item => {
        if (item.props.name === field && 
            item.props.linkage && 
            item.props.linkage.trigger === 'change') {
          this.handleLinkage(item, value);
        }
      });
    },
    
    // 处理联动逻辑
    handleLinkage(sourceItem, value) {
      const { trigger, actions } = sourceItem.props.linkage;

      if (!actions || !actions.length) return;
      actions.forEach(action => {
        const targetItem = this.formItems.find(
          item => item.props.name === action.targetField
        );
        if (!targetItem) return;
        
        // 执行条件判断
        let shouldExecute = true;
        if (action.condition) {
          try {
            // 创建安全的条件判断环境
            const condition = new Function('value', `return ${action.condition}`);
            shouldExecute = condition(value);
          } catch (error) {
            console.error('联动条件执行错误:', error);
            return;
          }
        }
                  console.log('action', action,shouldExecute);

        
        if (shouldExecute) {
          switch (action.type) {
            case 'show':
              targetItem.props.hidden = false;
              break;
            case 'hide':
              targetItem.props.hidden = true;
              break;
            case 'required':
              this.updateValidationRules(targetItem, true);
              break;
            case 'disabled':
              targetItem.props.disabled = true;
              break;
            case 'setValue':
              this.formData[targetItem.props.name] = action.value;
              break;
          }
        } else {
          switch (action.type) {
            case 'show':
              targetItem.props.hidden = true;
              break;
            case 'hide':
              targetItem.props.hidden = false;
              break;
            case 'required':
              this.updateValidationRules(targetItem, false);
              break;
            case 'disabled':
              targetItem.props.disabled = false;
              break;
            case 'setValue':
              this.formData[targetItem.props.name] = '';
              break;
          }
        }
      });
    },
    
    // 更新验证规则
    updateValidationRules(item, required) {
      const rules = item.props.rules || [];
      const requiredRule = rules.find(rule => rule.type === 'required');
      
      if (required && !requiredRule) {
        rules.push({
          type: 'required',
          message: '该字段为必填项'
        });
      } else if (!required && requiredRule) {
        const index = rules.indexOf(requiredRule);
        rules.splice(index, 1);
      }
      
      item.props.rules = rules;
    }
  }
}
</script>

<style lang="less" scoped>
.form-actions {
  text-align: center;
  margin-top: 24px;
}
</style> 